.App
{
  display:flex;
  justify-content: center;
  min-height: 100vh;
  align-items: center;
  background-color:#F08080;

}
.container
{
  display: flex;
  height: 80vh;
  border: 1px solid black;
  padding: 20px;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
  color:white
}
.todoForm{
  display: flex;
  justify-content: space-around;
  width: 450px;
}
input{
  width: 80%;
  padding: 10px;
  border-radius: 20px;
  border: None;
}

button{
  border:none;
  padding:10px;
  border-radius:20px;
  margin: 0 5px;
}
.alltodos
{
  list-style: none;
  margin:0;
  padding:0;
}
.singletodo
{
  border-radius: 50px;
  margin:10px 0;
  background-color:#4169E1;
  padding: 10px 20px;
  display: flex;
  width:450px;
  align-items: center;

}
.todotext
{
  flex: 1;

}

